html {
  margin: 0;
  height: 100vh;
}

body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #9370DB;
  height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.flex {
  display: flex;
}

.App {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 500px;
  height: 100%;
  padding: 1.5rem;
  background-color: white;
  border-radius: 0.3rem;
}

#text {
  text-align: center;
  line-height: 140%;
  font-size: x-large;
  padding-inline: 1rem;
  font-weight: 700;
}

#author {
  position: -webkit-sticky;
  position: sticky;
  left: 100%;
  padding: 1rem;
  font-style: italic;
}

#box-footer {
  width: 100%;
  height: 35px;
  justify-content: space-between;
}
.icon {
  height: 35px;
  width: 35px;
  margin: 0;
  padding: 0;
  color: #9370DB;
}

#new-quote {
  background-color: #9370DB;
  border: none;
  cursor: pointer;
  color: white;
  border-radius: .3rem;
  padding: .3rem;
  margin: .1rem;
  font-weight: 700;
  transition: .5s ease;
  color: black;
}

#new-quote:hover {
  background-color: #c9b3f5;
}

@media (max-width: 700px) {
  .App {
    width: 70%;
  }
}
